import { Texture } from 'three/src/textures/Texture';
export const getTexture = async () => {
    const img = new Image();
    const texture = new Texture();
    await new Promise(resolve => {
        img.onload = function () {
            texture.image = img;
            texture.needsUpdate = true;
            resolve(1);
        };
        img.setAttribute('src', `data:image/png;base64,`);
    });
    return texture;
};
