export const getWindowDimensions = () => {
    const innerWidth = Number(window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth);
    const innerHeight = Number(window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight);
    return {
        innerWidth,
        innerHeight
    };
};
export const scrollHeight = () => Number(document.documentElement.scrollHeight || document.body.scrollHeight);
export const scrollPosition = () => Number(document.documentElement.scrollTop || document.body.scrollTop);
export const randomInt = (min, max) => {
    return min + Math.floor(Math.random() * (max - min + 1));
};
export const randomIntMax = max => {
    return Math.floor(Math.random() * max);
};
